<template>
    <div class="visit-new">
        <el-form :model="visitForm" ref="visitForm" :rules="visitFormRule" label-width="100px" class="w575" size>
            <el-form-item label="客户名称：" prop="customer_name">
                <el-autocomplete v-model="visitForm.customer_name" :fetch-suggestions="querySearchCustomer" placeholder="请输入客户名称" :trigger-on-focus="false" @select="customerSelect" style="display:block;"></el-autocomplete>
            </el-form-item>
            <el-form-item label="所属项目：" prop="project_name">
                <el-autocomplete v-model="visitForm.project_name" :fetch-suggestions="querySearchProject" placeholder="请输入项目名称" :trigger-on-focus="false" @select="projectSelect" style="display:block;"></el-autocomplete>
            </el-form-item>
            <el-form-item label="拜访目的：" prop="visit_purpose">
                <el-input v-model="visitForm.visit_purpose" placeholder="请输入拜访目的"></el-input>
            </el-form-item>
            <el-form-item label="拜访时间：" prop="visit_time">
                <el-date-picker v-model="visitForm.visit_time" type="datetime" placeholder="选择日期时间" class="wp100" @change="onTimeChang">
                </el-date-picker>
            </el-form-item>
            <div class="box-flex">
                <el-form-item prop="linkman_name" label="拜访对象：">
                    <el-input v-model="visitForm.linkman_name" placeholder="请填写姓名" class="w150 mr15"></el-input>
                </el-form-item>
                <el-form-item prop="linkman_contact" label-width="0px" class="flex-one">
                    <el-input v-model="visitForm.linkman_contact" placeholder="请填写联系电话" class="flex-one"></el-input>
                </el-form-item>
            </div>
            <el-form-item prop="linkman_post" label="职务：">
                <el-input v-model="visitForm.linkman_post" placeholder="请填写拜访对象的职务"></el-input>
            </el-form-item>
            <el-form-item prop="visit_result" label="拜访结果：">
                <el-input type="textarea" :rows="3" v-model="visitForm.visit_result" placeholder="请填写拜访结果"></el-input>
            </el-form-item>
            <el-form-item label="推动计划：">
                <el-input type="textarea" :rows="3" v-model="visitForm.visit_promote" placeholder="请填写下一步的推动计划"></el-input>
            </el-form-item>
            <el-form-item label="资源支持：">
                <el-select v-model="visitForm.support_type" placeholder="请选择需要的支持">
                    <el-option v-for="item in supportType" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <div class="tc">
                    <el-button type="primary" @click.native="save">保存</el-button>
                    <el-button @click.native="cancel">取消</el-button>
                </div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import rule from '@/utils/validate-rule'
    import { supportType } from '../const'
    import {fmt} from '@/utils/util'
    import apis from '@/api'
    import moment from 'moment'
    export default {
        data() {
            return {
                visitForm: {
                    customer_id: '',
                    customer_name: '',
                    project_id: '',
                    project_name: '',
                    visit_purpose: '',
                    visit_time: '',
                    linkman_name: '',
                    linkman_contact: '',
                    linkman_post: '',
                    visit_result: '',
                    visit_promote: '',
                    support_type: '',
                },
                visitTime:'',
                supportType,
                visitFormRule: {
                    customer_name: [
                        rule.required({
                            message: '请输入客户名称'
                        }),
                        rule.max({
                            max: 50
                        })
                    ],
                    visit_purpose: [
                        rule.required({
                            message: '请输入客户名称'
                        })
                    ],
                    visit_time: [
                        rule.required({
                            message: '请选择拜访时间'
                        })
                    ],
                    linkman_name: [
                        rule.required({
                            message: '请输入姓名'
                        })
                    ],
                    linkman_contact: [
                        rule.required({
                            message: '请输入联系电话'
                        }),
                        {
                            validator: rule.phone
                        }
                    ],
                    linkman_post: [rule.required({
                        message: '请输入拜访对象职务'
                    })],
                    visit_result: [
                        rule.required({
                            message: '请填写拜访结果'
                        }),
                        rule.max({
                            message: '请填写拜访结果',
                            max: 1000
                        })
                    ],
    
                },
            }
        },
        computed:{
            visitId:function(){
                return this.$route.params.visitId
            }
        },
        mounted(){
            if(this.visitId){
                this.getVisitDetail()
            }
        },
        methods: {
            async save() {
                let vm = this
                vm.$refs.visitForm.validate((valid) => {
                    if (valid) {
                        let params = Object.assign({},this.visitForm)
                        params.visit_time = vm.visitTime
                        apis.visit.postNewVisit(params).then(() => {
                            vm.cancel()
                        })
                    } else {
                        return false
                    }
                })
    
            },
            async getVisitDetail(){
                let vm = this
                let result = await apis.visit.getVisitDetail(vm.visitId)
                result.visit_time = fmt.date(result.visit_time,"YYYY-MM-DD HH:mm:ss")
                vm.visitForm = Object.assign({},vm.visitForm,result)
            },
            cancel() {
                this.$router.push({
                    name: 'visitList'
                })
            },
            customerSelect(item) {
                let vm = this
                vm.visitForm = Object.assign({}, vm.visitForm, {
                    customer_name: item.customer_name,
                    customer_id: item.id
                })
            },
            async querySearchCustomer(query, cb) {
                let vm = this
                let visit_result = await apis.customer.getCustomerByName(this.visitForm.customer_name)
                cb(visit_result.map((item) => {
                    return {
                        ...item,
                        value: item.customer_name
                    }
                }))
            },
            projectSelect(item) {
                let vm = this
                vm.visitForm = Object.assign({}, vm.visitForm, {
                    project_name: item.project_name,
                    project_id: item.id
                })
            },
            async querySearchProject(query, cb) {
                let vm = this
                let visit_result = await apis.customer.getCustomerByName(this.visitForm.project_name)
                cb(visit_result.map((item) => {
                    return {
                        ...item,
                        value: item.project_name
                    }
                }))
            },
            onTimeChang(time){
                let tempTime = moment(time).format('YYYY-MM-DD HH:mm:ss')
                this.visitTime = fmt.getTime(tempTime)
            }
    
        }
    }
</script>

<style lang="scss" scope>
    .w575 {
        width: 575px;
    }
</style>


